"use client";

import { useEffect, useRef } from "react";

type LogEntry = {
    id: number;
    event: string;
    timestamp: string;
};

export default function EventLogger({ logs }: { logs: LogEntry[] }) {
    const containerRef = useRef<HTMLDivElement>(null);

    useEffect(() => {
        if (containerRef.current) {
            containerRef.current.scrollTop = containerRef.current.scrollHeight;
        }
    }, [logs]);

    return (
        <div className="flex flex-col h-full bg-slate-900 border-l border-slate-700 w-80 shadow-xl fixed right-0 top-0 bottom-0 z-50">
            <div className="p-4 border-b border-slate-700 bg-slate-800">
                <h2 className="text-sm font-bold text-emerald-400 uppercase tracking-wider flex items-center gap-2">
                    <span className="w-2 h-2 rounded-full bg-emerald-500 animate-pulse" />
                    Event Inspector
                </h2>
                <p className="text-xs text-slate-400 mt-1">
                    Monitor de eventos en tiempo real
                </p>
            </div>

            <div
                ref={containerRef}
                className="flex-1 overflow-y-auto p-2 font-mono text-xs space-y-1"
                // Event 17: onScroll
                onScroll={() => console.log("SCROLL EVENT DETECTED")}
                // Event 18: onWheel
                onWheel={() => console.log("WHEEL EVENT DETECTED")}
            >
                {logs.length === 0 && (
                    <div className="text-slate-600 text-center mt-10 italic">
                        Esperando eventos...
                    </div>
                )}
                {logs.map((log) => (
                    <div
                        key={log.id}
                        className="group flex gap-2 p-2 hover:bg-slate-800 rounded transition duration-75 border-l-2 border-transparent hover:border-blue-500"
                    >
                        <span className="text-slate-500 shrink-0 select-none">[{log.timestamp}]</span>
                        <span className="text-blue-300 font-semibold group-hover:text-blue-200">{log.event}</span>
                    </div>
                ))}
            </div>

            <div className="p-2 bg-slate-800 text-[10px] text-slate-500 text-center">
                Total Eventos: {logs.length}
            </div>
        </div>
    );
}
