"use client";

import { useState } from "react";
import EventLogger from "@/components/EventLogger";
import { createCliente, updateCliente, deleteCliente } from "@/app/actions";
import { Plus, Search, Edit3, Trash2, Info, Copy } from "lucide-react";

// Updated type definition based on REAL Introspected DB
type Cliente = {
    id: number;
    nombre: string;
    correo: string;
    telefono: string | null;
    direccion: string | null;
    fechaRegistro: Date;
};

export default function ClientDashboard({ clientes }: { clientes: Cliente[] }) {
    const [logs, setLogs] = useState<{ id: number; event: string; timestamp: string }[]>([]);
    const [editingId, setEditingId] = useState<number | null>(null);
    const [search, setSearch] = useState("");

    const logEvent = (eventName: string) => {
        const now = new Date();
        const timeString = `${now.getHours()}:${now.getMinutes()}:${now.getSeconds()}.${now.getMilliseconds()}`;
        setLogs((prev) => [
            ...prev,
            { id: Date.now(), event: eventName, timestamp: timeString },
        ]);
    };

    const filteredClientes = clientes.filter((c) =>
        c.nombre.toLowerCase().includes(search.toLowerCase())
    );

    return (
        <div className="flex bg-slate-100 min-h-screen">
            <div className="flex-1 p-8 mr-80">
                <header className="mb-8">
                    <h1 className="text-3xl font-bold text-slate-800">Planet Express CRM</h1>
                    <p className="text-slate-500">Gestión de Clientes v3000 (Next.js)</p>
                    <p className="text-xs text-slate-400">DB Schema: Cliente (nombre, correo, telefono)</p>
                </header>

                {/* --- DEMO AREA (Events 13-16) --- */}
                <section className="mb-8 p-6 bg-white rounded-lg shadow-sm border border-slate-200">
                    <div className="flex items-center gap-2 mb-4">
                        <Info
                            className="w-5 h-5 text-blue-500 cursor-help"
                            // Event 19: onContextMenu
                            onContextMenu={(e) => { e.preventDefault(); logEvent("onContextMenu"); }}
                        />
                        <h2 className="font-semibold text-slate-700">Zona de Pruebas de Texto</h2>
                    </div>
                    <textarea
                        className="w-full p-3 border rounded-md bg-slate-50 text-sm"
                        rows={3}
                        placeholder="Interactúa aquí: Copia, Pega, Corta, Selecciona..."
                        // Event 13: onCopy
                        onCopy={() => logEvent("onCopy")}
                        // Event 14: onCut
                        onCut={() => logEvent("onCut")}
                        // Event 15: onPaste
                        onPaste={() => logEvent("onPaste")}
                        // Event 16: onSelect
                        onSelect={() => logEvent("onSelect")}
                    />
                </section>

                <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">

                    {/* --- FORMULARIO --- */}
                    <div className="lg:col-span-1">
                        <div className="bg-white p-6 rounded-lg shadow-lg sticky top-8">
                            <h2 className="text-xl font-bold mb-4 text-slate-800 flex items-center gap-2">
                                {editingId ? <Edit3 size={20} /> : <Plus size={20} />}
                                {editingId ? "Editar Cliente" : "Nuevo Cliente"}
                            </h2>

                            <form
                                action={async (formData) => {
                                    if (editingId) {
                                        await updateCliente(editingId, formData);
                                        setEditingId(null);
                                    } else {
                                        await createCliente(formData);
                                    }
                                    logEvent("SERVER ACTION COMPLETED");
                                    // Reset form manually or handled by React 19 actions
                                    (document.getElementById("clientForm") as HTMLFormElement).reset();
                                }}
                                id="clientForm"
                                // Event 11: onSubmit
                                onSubmit={() => logEvent("onSubmit")}
                                // Event 12: onReset
                                onReset={() => { logEvent("onReset"); setEditingId(null); }}
                                // Event 21: onInvalid
                                onInvalid={() => logEvent("onInvalid")}
                            >
                                <div className="space-y-4">
                                    <div>
                                        <label className="block text-sm font-medium text-slate-600">Nombre Completo</label>
                                        <input
                                            name="nombre"
                                            type="text"
                                            required
                                            className="w-full mt-1 p-2 border rounded focus:ring-2 focus:ring-blue-500 outline-none"
                                            // Event 6: onInput
                                            onInput={() => logEvent("onInput (Nombre)")}
                                        />
                                    </div>
                                    <div>
                                        <label className="block text-sm font-medium text-slate-600">Correo</label>
                                        <input
                                            name="email"
                                            type="email"
                                            required
                                            className="w-full mt-1 p-2 border rounded focus:ring-2 focus:ring-blue-500 outline-none"
                                        />
                                    </div>
                                    <div>
                                        <label className="block text-sm font-medium text-slate-600">Teléfono</label>
                                        <input
                                            name="telefono"
                                            type="tel"
                                            className="w-full mt-1 p-2 border rounded focus:ring-2 focus:ring-blue-500 outline-none"
                                        />
                                    </div>

                                    <div className="flex gap-2 pt-2">
                                        <button
                                            type="submit"
                                            className="flex-1 bg-blue-600 hover:bg-blue-700 text-white p-2 rounded transition font-medium"
                                            // Event 1: onClick
                                            onClick={() => logEvent("onClick (Guardar)")}
                                            // Event 2: onMouseEnter
                                            onMouseEnter={() => logEvent("onMouseEnter")}
                                            // Event 3: onMouseLeave
                                            onMouseLeave={() => logEvent("onMouseLeave")}
                                        >
                                            {editingId ? "Actualizar" : "Guardar"}
                                        </button>
                                        <button
                                            type="reset"
                                            className="px-4 py-2 border border-slate-300 rounded text-slate-600 hover:bg-slate-50 transition"
                                            onClick={() => logEvent("onClick (Cancelar)")}
                                        >
                                            Cancelar
                                        </button>
                                    </div>
                                </div>
                            </form>
                        </div>
                    </div>

                    {/* --- LISTA --- */}
                    <div className="lg:col-span-2">
                        <div className="bg-white rounded-lg shadow-sm border border-slate-200 overflow-hidden">
                            <div className="p-4 border-b border-slate-100 flex items-center gap-2 bg-slate-50">
                                <Search className="text-slate-400 w-5 h-5" />
                                <input
                                    type="text"
                                    placeholder="Buscar clientes por nombre..."
                                    className="bg-transparent outline-none w-full text-sm"
                                    value={search}
                                    // Event 5: onChange
                                    onChange={(e) => { setSearch(e.target.value); logEvent("onChange (Search)"); }}
                                    // Event 7: onFocus
                                    onFocus={() => logEvent("onFocus (Search)")}
                                    // Event 8: onBlur
                                    onBlur={() => logEvent("onBlur (Search)")}
                                    // Event 9: onKeyDown
                                    onKeyDown={() => logEvent("onKeyDown")}
                                    // Event 10: onKeyUp
                                    onKeyUp={() => logEvent("onKeyUp")}
                                />
                            </div>

                            <table className="w-full text-sm text-left">
                                <thead className="bg-slate-50 text-slate-500 font-medium">
                                    <tr>
                                        <th className="p-3">ID</th>
                                        <th className="p-3">Nombre</th>
                                        <th className="p-3">Correo</th>
                                        <th className="p-3">Teléfono</th>
                                        <th className="p-3 text-right">Acciones</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {filteredClientes.map((cliente) => (
                                        <tr
                                            key={cliente.id}
                                            className="border-t border-slate-100 hover:bg-slate-50 transition cursor-pointer"
                                            // Event 4: onDoubleClick
                                            onDoubleClick={() => {
                                                setEditingId(cliente.id);
                                                logEvent(`onDoubleClick (Edit ID:${cliente.id})`);
                                            }}
                                            // Event 20: onPointerDown
                                            onPointerDown={() => logEvent("onPointerDown (Row)")}
                                        >
                                            <td className="p-3 font-mono text-slate-400">#{cliente.id}</td>
                                            <td className="p-3 font-medium text-slate-800">{cliente.nombre}</td>
                                            <td className="p-3 text-slate-600">{cliente.correo}</td>
                                            <td className="p-3 text-slate-500">{cliente.telefono}</td>
                                            <td className="p-3 text-right flex justify-end gap-2">
                                                <button
                                                    className="p-1 hover:bg-blue-100 text-blue-600 rounded"
                                                    title="Editar"
                                                    onClick={(e) => {
                                                        e.stopPropagation();
                                                        setEditingId(cliente.id);
                                                        logEvent("onClick (Edit Icon)");
                                                    }}
                                                >
                                                    <Edit3 size={16} />
                                                </button>
                                                <button
                                                    className="p-1 hover:bg-red-100 text-red-600 rounded"
                                                    title="Eliminar"
                                                    onClick={async (e) => {
                                                        e.stopPropagation();
                                                        if (confirm("¿Seguro?")) {
                                                            await deleteCliente(cliente.id);
                                                            logEvent("onClick (Delete)");
                                                        }
                                                    }}
                                                >
                                                    <Trash2 size={16} />
                                                </button>
                                            </td>
                                        </tr>
                                    ))}
                                </tbody>
                            </table>
                            {filteredClientes.length === 0 && (
                                <div className="p-8 text-center text-slate-400">
                                    No se encontraron clientes.
                                </div>
                            )}
                        </div>
                    </div>

                </div>
            </div>

            {/* Visual Console */}
            <EventLogger logs={logs} />
        </div>
    );
}
